import VueI18n from 'vue-i18n'
import en from './en'
import zh from './zh'
import Vue from 'vue'

Vue.use(VueI18n)

const DEFALUT_LOCALE = 'en'
export default new VueI18n({
  locale: DEFALUT_LOCALE,
  messages: {
    en,
    zh
  }
})



