import axios from 'axios'
const qs = require('qs')
// 设置超时时间
// axios.defaults.timeout = 15000 // 15秒
axios.defaults.baseURL = 'http://ofoapi-qa.ofo.com'
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8'
axios.defaults.transformRequest = [(data) => { return qs.stringify(data) }]

// axios 响应拦截
axios.interceptors.response.use(function (response) {
  const {errorCode: code, msg, values: data} = response.data
  const OK = code === 200
  if (code === 10003) { // token 过期
  }
  return {code, msg, data, OK}
  // 处理响应数据
}, function (error) {
  // 处理响应失败
  return Promise.reject(error)
})

export default axios



