import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'
import store from '../store'
Vue.use(Router)
const router = new Router({
  mode: 'history',
  routes
})
router.beforeEach((to, from, next) => {
  if (!store.getters.token &&
    !to.meta.login &&
    (!to.meta.noAuth || store.getters.isDeviceRegister)) {
    next({
      name: 'CellphoneScreen',
      replace: true,
      query: {
        to: to.name
      }})
  } else if (store.getters.unFinishedOrder && to.name !== 'RidingScreen') {
    next({name: 'RidingScreen', replace: true})
  } else if (!store.getters.unFinishedOrder && to.name === 'RidingScreen') {
    next({name: 'HomeScreen', replace: true})
  } else {
    next()
  }
})

router.afterEach((to) => {
  // 设置页面title
  (() => {
    if (to.meta && to.meta.title) {
      document.title = to.meta.title
    }
  })()
})

export default router



