export default {
  setKeepAliveInclude (state, value) {
    state.keepAliveInclude = value
  },
  setToken (state, value) {
    state.token = value
  },
  setCoordinate (state, value) {
    state.coordinate = value
  },
  setUnFinishedOrder (state, value) {
    state.unFinishedOrder = value
  },
  setIsDeviceRegister (state, value) {
    state.isDeviceRegister = value
  }
}



