import axios from '../axios'
import Fingerprintjs2 from 'fingerprintjs2'
// ofo接口的辅助参数
const lans = navigator.language.split('-')
const {width, height} = window.screen
// 接口的公共参数，获取一次，不会改变
const OFO_ASSIST_PARAMS = {
  languageCode: lans[0],
  languageArea: lans.length > 1 ? lans[1] : '',
  screenSize: `${width}*${height}`,
  source: 3,
  'source-version': 100,
  channel: 10000
}
const post = (url, params, noToken) => {
  params = {...params, ...OFO_ASSIST_PARAMS}
  const {token} = $app.$store.getters
  if (token && !params.token && !noToken) params.token = token
  let {coordinate} = $app.$store.getters
  return new Promise(async (resolve, reject) => {
    try {
      if (!params.token && !noToken) { // 需要登录的接口, 如果没传token，抛出异常
        reject(new Error('You are not logged in'))
      } else if (coordinate) {
        axios.post(url, {...params, ...coordinate}).then(resolve).catch(reject)
      } else {
        coordinate = await $app.$store.dispatch('asyncGetCoordinate')
        axios.post(url, {...params, ...coordinate}).then(resolve).catch(reject)
      }
    } catch (e) {
      reject(e)
    }
  })
}
// post请求带有idfa参数
const postWithIdfa = (url, params) => {
  return new Promise((resolve, reject) => {
    try {
      if (window.$idfa) {
        post(url, {...params, idfa: window.$idfa}, true).then(resolve).catch(reject)
      } else {
        new Fingerprintjs2().get(async (idfa) => {
          window.$idfa = idfa
          post(url, {...params, idfa: window.$idfa}, true).then(resolve).catch(reject)
        })
      }
    } catch (e) {
      reject(e)
    }
  })
}
// 获取临时token
const getTempToken = () => {
  return new Promise(async (resolve, reject) => {
    try {
      // 获取"游客"身份用户的token
      const {OK, msg, code, data: {token}} = await postWithIdfa(`/api/noSignUp/getToken`)
      if (OK) {
        window.$tempToken = token
        resolve(token)
      } else {
        if (code === 40077) { // 该设备已经被注册
          $app.$router.replace({name: 'CellphoneScreen'})
          $app.$store.commit('setIsDeviceRegister', true)
          $app.$toast.text('You have registered, please log in')
        }
        reject(new Error(msg))
      }
    } catch (e) {
      reject(e)
    }
  })
}
// 使用临时token请求API
const tempTokenPost = async (url, params) => {
  const {token = window.$tempToken} = $app.$store.getters
  return new Promise(async (resolve, reject) => {
    try {
      if (token) {
        const data = await post(url, {...params, token})
        if (data.OK) { // token 可用
          resolve(data)
        } else { // token过期 (重新拉取 临时token)
          const tempToken = await getTempToken()
          const data = await post(url, {...params, token: tempToken})
          resolve(data)
        }
      } else { // token 不存在 (获取临时token)
        const tempToken = await getTempToken()
        const data = await post(url, {...params, token: tempToken})
        resolve(data)
      }
    } catch (e) {
      reject(e)
    }
  })
}
export default {
  // 用户登录 {tel: '手机号', ccc: '手机编号，如86', countryCode：'地区编号，如CN', code: '验证码'}
  login (params) {
    return postWithIdfa(`/api/login_v2`, params)
  },
  // 用户注册 {email: '邮箱', tel: '手机号', ccc: '手机编号，如86', countryCode：'地区编号，如CN', code: '验证码'}
  register (params) {
    return postWithIdfa('/api/register_v2', params)
  },
  // 获取验证码 {tel: '', type: '0,注册; 1,登录'}
  getVerifyCode (params) {
    return post(`/verifyCode`, params, true)
  },
  // 获取附近单车信息
  nearbyofoCar () {
    return tempTokenPost(`/nearbyofoCar`)
  },
  // 获取未完成订单
  getUnfinished () {
    return post(`/unfinished`)
  },
  // 获取解锁码
  getUnlockCode (params) {
    return post(`/carno`, params)
  }
}



