const cmp = (path) => {
  return () => import(`./${path}`)
}

const CellphoneScreen = cmp('CellphoneScreen')
const VerifyCodeScreen = cmp('VerifyCodeScreen')
const SelectCountryScreen = cmp('SelectCountryScreen')
const HomeScreen = cmp('HomeScreen')
const ScanningScreen = cmp('ScanningScreen')
const TypingScreen = cmp('TypingScreen')
const UnlockingScreen = cmp('UnlockingScreen')
const UnlockedCodeScreen = cmp('UnlockedCodeScreen')
const RidingScreen = cmp('RidingScreen')
const basePath = '/ofo'
const routes = [
  {
    path: basePath + '/cellphone/:isSignUp?',
    name: 'CellphoneScreen',
    component: CellphoneScreen,
    meta: {
      title: 'Cellphone',
      login: true // 标识为登录模块
    }
  },
  {
    path: basePath + '/verify-code/:tel/:ccc/:countryCode/:email?',
    name: 'VerifyCodeScreen',
    component: VerifyCodeScreen,
    meta: {
      title: 'Verification Code',
      login: true // 标识为登录模块
    }
  },
  {
    path: basePath + '/select-country',
    name: 'SelectCountryScreen',
    component: SelectCountryScreen,
    meta: {
      title: 'Select a Country',
      noAuth: true
    }
  },
  {
    path: basePath + '/',
    name: 'HomeScreen',
    component: HomeScreen,
    meta: {
      title: 'Home',
      noAuth: true // 标识该页面，非登录用户（游客）可以访问
    }
  },
  {
    path: basePath + '/scanning',
    name: 'ScanningScreen',
    component: ScanningScreen,
    meta: {
      title: 'Scanning'
    }
  },
  {
    path: basePath + '/typing',
    name: 'TypingScreen',
    component: TypingScreen,
    meta: {
      title: 'Typing'
    }
  },
  {
    path: basePath + '/unlocking',
    name: 'UnlockingScreen',
    component: UnlockingScreen,
    meta: {
      title: 'Unlocking'
    }
  },
  {
    path: basePath + '/unlocked-code/:pwd/:carno?',
    name: 'UnlockedCodeScreen',
    component: UnlockedCodeScreen,
    meta: {
      title: 'Code for Bike'
    }
  },
  {
    path: basePath + '/riding',
    name: 'RidingScreen',
    component: RidingScreen,
    meta: {
      title: 'Riding Time'
    }
  }
]
export {routes}



