export default {
  // 用户登录
  login ({commit}, params) {
    return new Promise(async (resolve, reject) => {
      try {
        const data = await $API.ofo.login(params)
        resolve(data)
        const {OK, data: {token}} = data
        if (OK) {
          commit('setToken', token)
        }
      } catch (e) {
        reject(e)
      }
    })
  },
  // 获取用户位置
  asyncGetCoordinate ({commit}) {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        reject(new Error('Your browser does not support geolocation'))
        return
      }
      var options = { enableHighAccuracy: true, maximumAge: 0 }
      navigator.geolocation.getCurrentPosition(
        (position) => { // 定位成功
          var lat = parseFloat(position.coords.latitude)
          var lng = parseFloat(position.coords.longitude)
          const coordinate = {lng, lat}
          // todo 先将位置写死（新加坡），后续务必修改
          coordinate.lng = 103.85000
          coordinate.lat = 1.28000
          commit('setCoordinate', coordinate)
          resolve(coordinate)
        },
        (err) => { // 定位失败
          reject(err)
        },
        options)
    })
  },
  asyncGetUnFinishedOrderNo ({commit}) {
    return new Promise(async (resolve, reject) => {
      try {
        const {code, msg, data: order} = await $API.ofo.getUnfinished()
        if (code === 40003) {
          commit('setUnFinishedOrder', order || null)
          $app.setRootRoute({name: 'RidingScreen'})
          resolve(order)
        } else {
          $app.setRootRoute({name: 'HomeScreen'})
          reject(new Error(msg))
        }
      } catch (e) {
        reject(e)
      }
    })
  }
}



